.line {
  position: relative;
  height: 0.1875rem;
  width: 1.875rem;
  border-radius: 100px;
  background: var(--black);
}
.line:nth-child(2) {
  overflow: visible;
}
.line:nth-child(2)::before {
  position: absolute;
  z-index: 111;
  content: " ";
  width: 100%;
  height: 100%;
  transition: transform 200ms;
  background: black;
}
.line:nth-child(2)::after {
  position: absolute;
  z-index: 111;
  content: " ";
  width: 100%;
  height: 100%;
  transition: transform 200ms;
  background: black;
}

.open {
  background: transparent;
}
.open:nth-child(2)::before {
  transform: rotate(45deg);
  transition: transform 200ms;
}
.open:nth-child(2)::after {
  transition: transform 200ms;
  transform: rotate(-45deg);
}

